import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';

// Components
import Layout from '../components/Layout/Layout';
import BigTitle from '../components/BigTitle/BigTitle';
import More from '../components/More/More';

export const query = graphql`
	{
		file(relativePath: { eq: "img/aboutus.png" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
	}
`;
function AboutusPage({ data }) {
	return (
		<Layout title="درباره ما">
			<div className="container">
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<Img fluid={data.file.childImageSharp.fluid} alt="farm" style={{ width: '250px' }} />
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<BigTitle>درباره ما</BigTitle>
					</div>
				</div>
				<div className="pt-4 pb-4">
					<p dir="rtl" className="text-right">
						شرکت تجاری صنعتی ایران کشت نوین تنها فروشگاه اینترنتی معتبر در زمینه ارائه تخصصی ترین و با کیفیت
						ترین سموم برای دفع آفات موذی در ایران است.
					</p>
					<p dir="rtl" className="text-right">
						فروشگاه اینترنتی irankeshtnovin.ir از سال ۱۳۸۸ آغاز به کار کرده است.
					</p>
					<p dir="rtl" className="text-right">
						در این مدت، جمعی از فارغ التحصیلان رشته های حشره شناسی، زیست شناسی و بهداشت محیط از بهترین
						دانشگاه های ایران در ایران کشت نوین گرد هم جمع آمده اند. هدف کارشناسان ما این است تا در زمینه
						معرفی بهترین سموم دفع آفات موذی به صورت کاملاً حرفه ای و تخصصی، پیش قدم و تأثیر گذار باشند.
					</p>
					<p dir="rtl" className="text-right">
						نتیجه تلاش کارشناسان ما، در قالب مقالات تخصصی برای هر آفت در آدرس اینترنتی irankeshtnovin.ir در
						دسترس است.
					</p>
					<p dir="rtl" className="text-right">
						شما می توانید با ورود به سایت، تمام مطالب مهم در مورد انواع حشرات و جوندگان و بهترین روش های
						نابودی و مبارزه با آن ها را بررسی کنید.
					</p>
				</div>
				<div className="pt-4 pb-4">
					<More />
				</div>
			</div>
		</Layout>
	);
}

export default AboutusPage;
